import React from 'react'
import SectionBanner from '../../components/Banner';
import './Customer.css';
import CustomerCard from '../../components/customer_card';
import SignupRectangle from '../../components/signup_rectange';
import 'bootstrap/dist/css/bootstrap.min.css';


const Customer = () => {
    return (
        <div>
            <SectionBanner
                title={'Customers'}
                tagline={"Get an insider's perspective on how our customers achieve superior travel management results."}
            />
            <div className='cardsSection'>
                <CustomerCard
                    image={require('../../assets/customer/fidson_logo.png')}
                    text={"Kardinal has made our lives easier by providing easy-to-use reports, mobile responsiveness, and quick response times."}
                    subText={"Thompson"}
                    subText1={"Fidson Healthcare PLC"}
                ></CustomerCard>
                <CustomerCard
                    image={require('../../assets/customer/kema_logo.png')}
                    text={"Travel for the entertainment industry can be quite daunting. However, with Kardinals platform, I easily derive all my invoices and send them to clients."}
                    subText={"Kema"}
                    subText1={"Kema Oyih Management"}
                ></CustomerCard>
                <CustomerCard
                    image={require('../../assets/customer/eze_logo.png')}
                    text={"No travel agency understands entertainment travel like Kardinal. From start to finish, I do not bother at all and that is a great thing."}
                    subText={"Eze"}
                    subText1={"Bean Creative"}
                ></CustomerCard>
                <CustomerCard
                    image={require('../../assets/customer/faith_logo.png')}
                    text={"We really love the simplicity of Kardinals reporting and the product. Response time and customer support is also one of the best."}
                    subText={"Faith"}
                    subText1={"Parthian Partners"}
                ></CustomerCard>
                <CustomerCard
                    image={require('../../assets/customer/chimdimma_logo.png')}
                    text={"Signing up on Kardinal gave us the flexiblilty on staff travel which is what we needed as a fast paced business."}
                    subText={"Chimdimma"}
                    subText1={"The Initiative for Equal rights"}
                ></CustomerCard>
                <CustomerCard
                    image={require('../../assets/customer/zaynab_logo.png')}
                    text={"Kardinal provides payment alternatives for us. This has made travel even more seamless for our company."}
                    subText={"Zaynab"}
                    subText1={"TASCK"}
                ></CustomerCard>
                <CustomerCard
                    image={require('../../assets/customer/anthonia_logo.png')}
                    text={"Kardinal already does all the hard work required for me to book and manage all my travel activities."}
                    subText={"Anthonia"}
                    subText1={"JNCI"}
                ></CustomerCard>
                <CustomerCard
                    image={require('../../assets/customer/desmond_logo.png')}
                    text={"We have had so much trouble reconciling travel expenses, but Kardinal gives us a simple solution to this problem."}
                    subText={"Desmond"}
                    subText1={"Chocolate City Music"}
                ></CustomerCard>
                <CustomerCard
                    image={require('../../assets/customer/ebuka_logo.png')}
                    text={"As a business owner, it can get very difficult searching for my preferred flight option from different websites until I came across Kardinal."}
                    subText={"Ebuka"}
                    subText1={"Maliko Studios"}
                ></CustomerCard>
            </div>
            <SignupRectangle />
        </div>
    )
}

export default Customer