import React from 'react';
import './Privacy.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container } from 'react-bootstrap';
import PrivacyContent from '../../components/PrivacyContent';
import { useState } from 'react';

const Privacy = () => {
    const [isActive, setIsActive] = useState(1);

    const handleClick = (e) => {
        setIsActive(e);
    };
    return (
        <div>
            <section className='privacy-head'>
                <div className='privacy-background'>
                    <h1>
                        Privacy
                    </h1>
                    <p>
                        Get an insider's perspective on how our customers achieve superior travel management results.
                    </p>
                </div>
            </section>

            <Container>
                <div className='head'>
                    <p className={isActive === 1 ? 'tabActive' : ""} onClick={() => handleClick(1)}>Terms Of Service</p>
                    <p className={isActive === 2 ? 'tabActive' : ""} onClick={() => handleClick(2)}>Security</p>
                    <p className={isActive === 3 ? 'tabActive' : ""} onClick={() => handleClick(3)}>Privacy Policy</p>
                </div>
                <PrivacyContent />
            </Container>
            <div className='gradientRectangle'>
                <p>
                    {"Experience business travel made for"}<br></br>{"businesses like yours."}
                </p>
                <div>
                    <input placeholder='Your work email' />
                    <button className='buttonFull'>Sign up for free</button>
                    <button className='buttonSmall'>Sign up</button>
                </div>
            </div>
        </div>
    )
}

export default Privacy;
