import React from 'react';

const ReqDemoAndSignupSection = ({ title, signUpBtn, reqDemoBtn, bgColor={background: "linear-gradient(271.26deg,#243256 48.77%,rgba(36, 50, 86, 0.53) 100.67%)"} }) => {
    return (
        <section style={bgColor} className='reqDemoAndSignup__Section'>
            <h4>{title}</h4>
            <div>
                <button>{signUpBtn}</button>
                <button>{reqDemoBtn}</button>
            </div>
        </section>
    );
};
export default ReqDemoAndSignupSection;