import React from 'react';
import SectionBanner from '../../components/Banner';
import './businessSeries.css';
// Bootstrap
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
// Components
import { LightRoundBtn, RegisterFilledBtn } from '../../helper/buttons';
import { CustomTypoH1, CustomTypoH2, CustomTypoPara } from '../../helper/headings';
// Mui icon
import CalendarMonthOutlinedIcon from '@mui/icons-material/CalendarMonthOutlined';
import BusinessSeriesCard from '../../components/business_series_card';
import ReqDemoAndSignupSection from '../../components/req_demo_or_signup_section';

const BusinessSeries = () => {
    return (
        <div>
            {/* Custom banner */}
            <SectionBanner
                title={'Business Series'}
                tagline={'The best online resources for African businesses by Africans'}
            />

            <section className='business-series__section2'>
                <Container fluid>
                    <Row>
                        <Col xs={12} sm={12} md={6} lg={6} xl={6} className='business-series__sub1'>
                            <img src={require('../../assets/Rectangle540-hotel.png')} alt='Rectangle540-hotel' />
                        </Col>
                        <Col xs={12} sm={12} md={6} lg={6} xl={6} className='business-series__sub2'>
                            <LightRoundBtn title={"Upcoming Session"} bg="#E8F0FF" />
                            <CustomTypoH1 typo={"Leveraging The Right Tool For Your Business Travel"} />
                            <div className='date-grid'>
                                <CalendarMonthOutlinedIcon sx={{ color: '#655F5F' }} />
                                <h5>September 02, 2022 | 11 am</h5>
                            </div>
                            <CustomTypoPara typo={"Kardinal Business Series interviews top African business professionals, where they discuss different problems and sustainable solutions for businesses, especially in Africa."} />
                            <CustomTypoPara typo={"As part of this discussion, we will examine innovative solutions that can be applied to existing ones in order to help businesses maximize their output."} />
                            <RegisterFilledBtn title={'Register Now'} />
                        </Col>
                    </Row>
                </Container>
            </section>

            <section className='business-series__section3'>
                <LightRoundBtn title={"Previous Sessions"} bg="#D3D4F8" />
                <CustomTypoH2 typo={"Catch all the highlights on our business series"} />

                <div className='business-series-card-list'>
                    <BusinessSeriesCard
                        image={require('../../assets/Rectangle545-CardBS.png')}
                        date={'November 17, 2021 | 11 am'}
                        des={'Step up your business using bleisure programs with Oluchi Obiejesi and Yyvonne Joseph.'}
                    />
                    <BusinessSeriesCard
                        image={require('../../assets/Rectangle547-CardBS.png')}
                        date={'September 08, 2021 | 11 am'}
                        des={'Improving employee engagement and experience with Modupe Bankole and Temitope Aina'}
                    />
                    <BusinessSeriesCard
                        image={require('../../assets/Rectangle545-CardBS.png')}
                        date={'September 08, 2021 | 11 am'}
                        des={'Step up your business using bleisure programs with Oluchi Obiejesi and Yyvonne Joseph.'}
                    />
                </div>
            </section>

            <section className='business-series__section4'>
                <Container fluid>
                    <Row>
                        <Col xs={12} sm={12} md={6}>
                            <CustomTypoH1 typo={`Get front seats to our next business series.`} />
                            <p className='business-series__section4_para'>Sign up and you will be the first to know when we host our next Corporate Live Series</p>
                        </Col>
                        <Col xs={12} sm={12} md={6}>
                            <div className='business-series__registration_form'>
                                <input placeholder='Full name' />
                                <input placeholder='Email' />
                                <button>Register</button>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </section>

            <ReqDemoAndSignupSection
                title={`Supercharge your business with the right travel tools`}
                signUpBtn={`Sign up for free`}
                reqDemoBtn={`Request Demo`}
            />
        </div>
    );
};

export default BusinessSeries;