import React from 'react';
import Accordion from 'react-bootstrap/Accordion';
import { QUESTION_ANSWER } from '../Constants/FAQs';

function FAQTabs({activeKey}) {
    return (
        <Accordion defaultActiveKey="0">
            {QUESTION_ANSWER[activeKey]?.map((item, index) => (
                <Accordion.Item eventKey={index} key={index}>
                    <Accordion.Header>{item.question}</Accordion.Header>
                    <Accordion.Body className='accordian-collapse-chev'>{item.answer}</Accordion.Body>
                </Accordion.Item>
            ))}
        </Accordion>
    );
}

export default FAQTabs;