import React, { useState } from 'react';
// Bootstrap
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
// Mui icon
import AddIcon from '@mui/icons-material/Add';
import MinimizeIcon from '@mui/icons-material/Minimize';
import Divider from '@mui/material/Divider';

const Footer = () => {
    const [collapse, setCollapse] = useState(null);

    return (
        <footer>
            <Container>
                <Row className='desktop-row'>
                    <Col sm={12} md={12} lg={3}>
                        <img className='footer-logo' src={require('../assets/AppLogo.png')} alt='AppLogo' />
                    </Col>
                    <Col sm={6} md={3} lg={2}>
                        <h4>Product</h4>
                        <ul>
                            <li>Flight</li>
                            <li>Accommodation</li>
                            <li>Car</li>
                            <li>K Care</li>
                            <li>Expense Management</li>
                        </ul>
                    </Col>
                    <Col sm={6} md={3} lg={2}>
                        <h4>Company</h4>
                        <ul>
                            <li>Customers</li>
                            <li>About</li>
                            <li>Privacy</li>
                            <li>Terms</li>
                            <li>Contact us</li>
                        </ul>
                    </Col>
                    <Col sm={6} md={3} lg={2}>
                        <h4>Resources</h4>
                        <ul>
                            <li>Guides</li>
                            <li>Blog</li>
                            <li>Business Series</li>
                            <li>Help/FAQ</li>
                        </ul>
                    </Col>
                    <Col sm={6} md={3} lg={2}>
                        <h4>Contact</h4>
                        <ul>
                            <li>Call: +234 802 6993 460</li>
                            <li>Whatsapp: Chat us now</li>
                            <li>Bookings@gokardinal.com</li>
                            <li>Wecare@gokardinal.com</li>
                        </ul>
                    </Col>
                </Row>
                <Row className='mobile-row'>
                    <Col sm={12} md={12} lg={3}>
                        <img className='footer-logo' src={require('../assets/AppLogo.png')} alt='AppLogo' />
                    </Col>
                    <Col sm={6} md={3} lg={2}>
                        <div className='direction-row-corners p-right'>
                            <h4>Product</h4>
                            {collapse === 1 ?
                                <MinimizeIcon sx={{ color: '#40798C' }} onClick={() => setCollapse(null)} />
                                :
                                <AddIcon sx={{ color: '#40798C' }} onClick={() => setCollapse(1)} />
                            }
                        </div>
                        {collapse === 1 &&
                            <ul>
                                <li>Flight</li>
                                <li>Accommodation</li>
                                <li>Car</li>
                                <li>K Care</li>
                                <li>Expense Management</li>
                            </ul>
                        }
                    </Col>
                    <Col sm={6} md={3} lg={2}>
                        <div className='direction-row-corners p-right'>
                            <h4>Company</h4>
                            {collapse === 2 ?
                                <MinimizeIcon sx={{ color: '#40798C' }} onClick={() => setCollapse(null)} />
                                :
                                <AddIcon sx={{ color: '#40798C' }} onClick={() => setCollapse(2)} />
                            }
                        </div>
                        {collapse === 2 &&
                            <ul>
                                <li>Customers</li>
                                <li>About</li>
                                <li>Privacy</li>
                                <li>Terms</li>
                                <li>Contact us</li>
                            </ul>
                        }
                    </Col>
                    <Col sm={6} md={3} lg={2}>
                        <div className='direction-row-corners p-right'>
                            <h4>Resources</h4>
                            {collapse === 3 ?
                                <MinimizeIcon sx={{ color: '#40798C' }} onClick={() => setCollapse(null)} />
                                :
                                <AddIcon sx={{ color: '#40798C' }} onClick={() => setCollapse(3)} />
                            }
                        </div>
                        {collapse === 3 &&
                            <ul>
                                <li>Guides</li>
                                <li>Blog</li>
                                <li>Business Series</li>
                                <li>Help/FAQ</li>
                            </ul>
                        }
                    </Col>
                    <Col sm={6} md={3} lg={2}>
                        <div className='direction-row-corners p-right'>
                            <h4>Contact</h4>
                            {collapse === 4 ?
                                <MinimizeIcon sx={{ color: '#40798C' }} onClick={() => setCollapse(null)} />
                                :
                                <AddIcon sx={{ color: '#40798C' }} onClick={() => setCollapse(4)} />
                            }
                        </div>
                        {collapse === 4 &&
                            <ul>
                                <li>Call: +234 802 6993 460</li>
                                <li>Whatsapp: Chat us now</li>
                                <li>Bookings@gokardinal.com</li>
                                <li>Wecare@gokardinal.com</li>
                            </ul>
                        }
                    </Col>
                </Row>
            </Container>
            <Divider />
            <Container>
                <Row className='footer-last-section-row'>
                    <div>
                        <img src={require('../assets/LogoIcon.png')} alt='LogoIcon' />
                        <h5>Travel ecosytem for businesses in Africa</h5>
                    </div>
                    <div>
                        <img src={require('../assets/LinkedInIcon.png')} alt='' />
                        <img src={require('../assets/TwitterIcon.png')} alt='' />
                        <img src={require('../assets/FbIcon.png')} alt='' />
                        <img src={require('../assets/InstaIcon.png')} alt='' />
                        <img src={require('../assets/SocialIcon.png')} alt='' />
                    </div>
                    <div>
                        <p>2a, Alara street, Sabo Yaba. Lagos Nigeria</p>
                        <p> 2022 Kardinal Technologies, Inc.</p>
                        <p>All rights reserved.</p>
                    </div>

                </Row>
            </Container>
        </footer>
    );
};

export default Footer;