import React from "react";
import '../styles/components.css';


const HotelCardList = (props) => {
    const { image, text } = props

    return (
        <div className="list-box">

            <section className='hotel-card-list'>
                <img className='circle-image' src={image} alt="Customer Pic" />
                <p>
                    {text}
                </p>
                <img className='triangle-icon' src={require("../assets/hotel/triangle_list_icon.png")} alt="Icon" />
            </section>
            <hr style={{ width: "61.82px" }} />
            <hr style={{ width: "92.47px" }} />
            <hr style={{ width: "74.08px" }} />
        </div>
    )
}

export default HotelCardList;