import React from 'react';
import './travel.css';
import { Col, Row } from 'react-bootstrap';

import 'bootstrap/dist/css/bootstrap.min.css';
import { CustomTypoH2 } from '../../helper/headings';

const Travel = () => {
    return (
        <div>
            <br />
            <br />
            <br />
            <br />
            <Row >
                <Col xl={5} lg={5} md={5} sm={12} xs={12} className="section1-image">
                    <div className='blue-gradient-rectangle'>
                    </div>
                    <img src={require("../../assets/travel/section1_man_image.png")} alt="hall" />
                </Col>
                <Col xl={5} lg={5} md={5} sm={12} xs={12} className="">
                    <CustomTypoH2
                        typo="Simple, affordable, flexible business travel solution."
                    />

                    <div>
                        <section className='travel-card'>
                            <img src={require("../../assets/travel/book_icon.png")} alt="book" />
                            <h4>
                                Book
                            </h4>
                            <p>
                                Book cheaply, easily, and securely with our AI-powered booking engine. Powered by our extensive car, flight, and hotel inventory.
                            </p>
                        </section>
                    </div>

                    {/* <h1>
                        Personalized solution for business accommodation
                    </h1>
                    <p>
                        Find the right reservation (short stays, long stays, etc.) without compromising your expectations. Our platform offer hotels that comply with your policies as well as your preferences.
                    </p> */}
                </Col>
            </Row>

        </div>
    )
}
export default Travel;