import React from 'react';
import SectionBanner from '../../components/Banner';
import ReqDemoAndSignupSection from '../../components/req_demo_or_signup_section';
import { Col, Container, Row } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';
import { CustomTypoH1, CustomTypoH4, CustomTypoH5 } from '../../helper/headings';
// Css
import './contact.css';
const ContactPage = () => {
  return (
    <div>
      <SectionBanner
        title={`Contact`}
        tagline={`We would love to hear from you. Have any questions? Feel free reach out.`}
      />

      <section className='contact-page-section'>
        <Container>
          <Row>
            <Col style={{ maxWidth: "523px" }}>
              <CustomTypoH1 typo={`Questions or Enquiries?`} />
              <CustomTypoH5 typo={`Our customer support team is available 7 days a week from 5 am to 8 pm with live chat or by phone.`} />
            </Col>
          </Row>
          <Row className='margin-top-60'>
            <Col sm={12} md={6} lg={6}>
              <CustomTypoH4 typo={'Take advantage of what Kardinal has to offer'} />
              <CustomTypoH5 typo={`Do you want to learn more about Kardinal and what we have to offer? Let our team show you around.`} />
              <NavLink to='#' className='NavLink-btn'>Request demo now</NavLink>
            </Col>
            <Col sm={12} md={6} lg={6} className='padding-left-per'>
              <CustomTypoH4 typo={'Email Support?'} />
              <CustomTypoH5 typo={`Send an email and get a response quickly.`} />
              <div className='link-list'>
                <NavLink to='#' className='NavLink-btn'>Booking</NavLink>
                <NavLink to='#' className='NavLink-btn'>Support</NavLink>
                <NavLink to='#' className='NavLink-btn'>Sales</NavLink>
              </div>
            </Col>
          </Row>
          <Row className='margin-top-60'>
            <Col sm={12} md={6} lg={6}>
              <CustomTypoH4 typo={'Follow us on all platforms'} />
              <CustomTypoH5 typo={`Stay updated with all things Kardinal.`} />
              <div className='link-list'>
                <NavLink to='#' className='NavLink-btn'>NavLinkedin</NavLink>
                <NavLink to='#' className='NavLink-btn'>Twitter</NavLink>
                <NavLink to='#' className='NavLink-btn'>Instagram</NavLink>
                <NavLink to='#' className='NavLink-btn'>Facebook</NavLink>
              </div>
            </Col>
            <Col sm={12} md={6} lg={6} className='padding-left-per'>
              <CustomTypoH4 typo={'Visit us'} />
              <CustomTypoH5 typo={`2a Alara street, Sabo Yaba, Lagos Nigeria.`} />
            </Col>
          </Row>
        </Container>
      </section>

      <ReqDemoAndSignupSection
        title={`Change the way you do business travel`}
        signUpBtn={'Sign up for free'}
        reqDemoBtn={`Request Demo`}
      />
    </div>
  );
};

export default ContactPage;