import React from 'react'
import '../styles/components.css'
import 'bootstrap/dist/css/bootstrap.min.css';
const CustomerCard = (props) => {
    const { image, text, subText, subText1 } = props
    return (
        <div className='cardContainer'>
            <div className='card' style={{ width: "20.875em", height: "18.6875em" }}>
                <div className="card-body" >
                    <img src={image} width={"70px"} alt={subText + " logo"} />
                    <br />
                    <br />
                    <p className="card-text">{text}</p>
                    <p className='card-text cardSubtitle'><b>{subText}</b><br /><span style={{ color: "#655F5F" }}>{subText1}</span>
                    </p>
                </div>
            </div>
        </div>
    )
}

export default CustomerCard;