import React from 'react';
import { Container, Col, Row } from 'react-bootstrap';
import './home.css';
const Home = () => {
  return (
    <main>
      <section className='home-section-01'>
        <Container>
          <Row>
            <Col md={6} xl={6}>
              <h1>Save time and money for your business travel.</h1>
              <p>A simple dashboard for managing your travel, expenses and much more.</p>
              <div>
                <input />
                <button>Request demo</button>
              </div>
              <ul>
                <li>No extra fee</li>
                <li>Reconciliation and management</li>
                <li>Credit</li>
              </ul>
            </Col>
          </Row>
        </Container>
      </section>
      <div className='home-polygon' />
    </main>
  );
};
export default Home;